* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100px;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.box {
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  flex-grow: 1;
  flex-basis: 30%;
  height: 300px;
  min-width: 300px;
  color: #fff;

}

.box--1 {
  background-color: #f00;
}

.box--2 {
  background-color: #c00;
}

.box--3 {
  background-color: #900;
}

.box--4 {
  background-color: #600;
  order: 6;
}

.box--5 {
  background-color: #300;
  order: 5;
}

.box--6 {
  background-color: #000;
  order: 4;
}

@media screen and (max-width: 899px) {
  .box--3 {
    order: 4;
  }

  .box--4 {
    order: 3;
  }

  .box--5 {
    order: 5;
  }

  .box--6 {
    order: 6;
  }
}

@media screen and (max-width: 599px) {
  .box--3 {
    order: 3;
  }

  .box--4 {
    order: 4;
  }
}
